<template>
 	<div>
 		<!--导航-->
 		<header-nav></header-nav>
 		
 		<!--面包屑-->
 		<div class="breadcrumb">
 			<router-link to="/">首页</router-link>
 			<span :style="{color:'#97a8be'}">{{flag}}</span>
 		</div>
 		
 		<!--内容-->
 		<transition name="fade" mode="out-in">
 			<router-view></router-view>
 		</transition>
 	</div>
</template>

<script>
	
	import Header from '@/components/header'
	
	let flags={
		project: '我的项目',
    	workbench: '工作台',
    	doc: '文档'
	}
	
	export default{
		data(){
			return {
				flag:''
			}
		},
		watch:{
			$route(){
				this.flag=flags[this.$route.path.slice(1)]
			}
		},
		beforeRouteEnter(to,from,next){ //只有页面结构生成的时候执行一次
			next((vm)=>{//vm是指当前的vue实例，不是根实例
				vm.flag=flags[to.path.slice(1)]
			})
		},
		components:{
			/*headerNav:(resolve)=>{//模拟按需加载 
				setTimeout(()=>{
					resolve(Header)
					//resolve(@/components/header)
				},2000)
			}*/
			headerNav:Header
		}
	}
</script>
<style>

</style>



